import React, {useEffect, useState} from 'react';
import axios from 'axios';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { Show_alerta } from '../functions';

const ShowDeducciones = () => {
    const url='http://api.test/deduccionController.php';
    const [deducion,setDeducion]= useState([]);
    const [id_deducciones,setId_deducciones]= useState('');
    const [nombre,setNombre]= useState('');
    const [D_salario,setD_salario]= useState('');
    const [estado,setEstado]= useState('');
    const [operacion,setOperacion]= useState(1);
    const [title,setTitle]= useState('');

    useEffect( ()=>{
        getDeduccion();
    },[]);

    const getDeduccion = async () => {
        const respuesta = await axios.get(url);
        setDeducion(respuesta.data);
    }

    const openModal_3 = (op,id_deducciones,nombre,D_salario,estado) =>{
        setId_deducciones('');
        setNombre('');
        setD_salario('');
        setEstado('');
        setOperacion(op);
        if (op===1) {
            setTitle('Registrar Deduccion');
        }
        else if(op=== 2){
            setTitle('Editar Deduccion');
            setId_deducciones(id_deducciones);
            setNombre(nombre);
            setD_salario(D_salario);
            setEstado(estado);

        }
        window.setTimeout(function(){
            document.getElementById('nombre').focus();
        },500);
    }

    const validar = () => {
        var parametros;
        var metodo;
         if(nombre.trim() === ''){
            Show_alerta('Escribe el nombre de la deduccion','warning');
        }
        else if(D_salario === ''){
            Show_alerta('Selecciona si depende de salario','warning');
        }
        else if(estado === ''){
            Show_alerta('Selecciona el estado de la deduccion','warning');
        }
        else {
            if(operacion === 1){
                parametros= {nombre:nombre.trim(),D_salario:D_salario.trim(),estado:estado.trim()}
                metodo='POST';
            }
            else{
                parametros= {id_deducciones:id_deducciones,nombre:nombre.trim(),D_salario:D_salario.trim(),estado:estado.trim()}
                metodo='PUT';
            }
            enviarSolicitud(metodo,parametros);
        }
    }
    const enviarSolicitud = async(metodo,parametros) => {
        await axios({method:metodo, url: url, data:parametros}).then(function(respuesta){
            var tipo = respuesta.data[0];
            var msj = respuesta.data[1];
            Show_alerta(msj,tipo);
            if (tipo ==='success'){
                document.getElementById('btnCerrar').click();
                getDeduccion();
            }   
        })
        .catch(function(error){
            Show_alerta('Error en la solicitud', 'error');
            console.log(error);
        });
    }

    const deleteDeduccion= (id_deducciones,nombre) =>{
        const MySwal = withReactContent(Swal);
        MySwal.fire({
            title:'Seguro de eliminar la deduccion '+nombre+' ?',    
            icon: 'question',text:'No se podra dar marcha atras',
            showCancelButton:true,confirmButtonText:'Si, eliminar',cancelButtonText:'Cancelar'

        }).then((result) =>{
            if(result.isConfirmed){
                setId_deducciones(id_deducciones);
                enviarSolicitud('DELETE',{id_deducciones:id_deducciones});
            }
            else{
                Show_alerta('La deduccion NO fue eliminada','info');
            }
        });
    }


  return (
    <div className='App'>
        <div className='container-fluid'>
            <div className='row mt-3'>
                <div className='col-md-4 offset-md-4'>
                    <div className='d-grid mx-auto'>
                        <button onClick={()=> openModal_3(1)} className='btn btn-dark' data-bs-toggle='modal' data-bs-target='#modaldeduccion'>
                            <i className='fa-solid fa-circle-plus'></i> Aadir Deduccion
                        </button>
                    </div>
                </div>
            </div>
            <div className='row mt-3'>
                <div className='col-12 col-lg-8 offset-0 offset-lg-2'>
                    <div className='table-responsive'>
                        <table className='table table-bordered'>
                            <thead>
                                <tr><th>#</th><th>Nombre</th><th>Depende de Salario</th><th>Estado</th></tr>
                            </thead>
                            <tbody className='table-group-divider'>
                                {deducion.map( (deduccion)=>(
                                    <tr key={deduccion.id_deducciones}>
                                        <td>{deduccion.id_deducciones}</td>
                                        <td>{deduccion.nombre}</td>
                                        <td>{deduccion.D_salario}</td>
                                        <td>{deduccion.estado}</td>
                                        <td>
                                            <button onClick={() => openModal_3(2,deduccion.id_deducciones,deduccion.nombre,deduccion.D_salario,deduccion.estado)} 
                                                    className='btn btn-warning' data-bs-toggle='modal' data-bs-target='#modaldeduccion'>
                                                <i className='fa-solid fa-edit'></i>
                                            </button>
                                            &nbsp;
                                            <button onClick={()=>deleteDeduccion(deduccion.id_deducciones,deduccion.nombre)} className='btn btn-danger'>
                                                <i className='fa-solid fa-trash'></i>
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody> 
                            
                           
                        </table>
                    </div>
                </div>
            </div>

        </div>
        
        <div id='modaldeduccion' className='modal fade' aria-hidden='true'>
            <div className='modal-dialog'>
                <div className='modal-content'>
                    <div className='modal-header'>
                        <label className='h5'>{title}</label>
                        <button type='button' className='btn-close' data-bs-dismiss='modal' aria-label='close'></button>
                    </div>
                    <div className='modal-body'>
                        <input type='hidden' id='id'></input>
                        
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-clipboard'></i></span>
                            <input type='text' id='nombre' className='form-control' placeholder='Nombre' value={nombre}
                            onChange={(e)=> setNombre(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-dollar-sign'></i></span>
                            <select id='D_salario' className='form-control' placeholder='Salario' value={D_salario}
                            onChange={(e)=> setD_salario(e.target.value)}>
                                <option hidden selected>Depende de Salario?</option>

                                <option>Si</option>

                                <option selected>No</option>
                            </select>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-list-ol'></i></span>
                            <select id='estado' className='form-control' placeholder='Estado' value={estado}
                            onChange={(e)=> setEstado(e.target.value)}>
                                <option hidden selected>Estado</option>

                                <option>Disponible</option>

                                <option selected>No Disponible</option>
                            </select>
                        </div>
                        <div className='d-grid col-6 mx-auto'>
                            <button onClick={() => validar()}  className='btn btn-success'>
                                <i className='fa-solid fa-floppy-disk'></i>Guardar
                            </button>
                        </div>

                    </div>
                    <div className='modal-footer'>
                        <button type='button' id='btnCerrar' className='btn btn-secondary' data-bs-dismiss='modal'>Cerrar</button>
                    </div>
                </div>
            </div>

        </div>
    </div>
  )
}

export default ShowDeducciones