import React, {useEffect, useState} from 'react';
import axios from 'axios';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { Show_alerta } from '../functions';

const ShowEmpleado = () => {
    const url='http://api.test/nominacontroller.php';
    const [empleados,setEmpleados]= useState([]);
    const [id_empleado,setId_empleado]= useState('');
    const [cedula,setCedula]= useState('');
    const [nombre,setNombre]= useState('');
    const [salario,setSalario]= useState('');
    const [id_nomina,setId_nomina]= useState('');
    const [operacion,setOperacion]= useState(1);
    const [title,setTitle]= useState('');

    useEffect( ()=>{
        getEmpleados();
    },[]);

    const getEmpleados = async () => {
        const respuesta = await axios.get(url);
        setEmpleados(respuesta.data);
    }

    const openModal = (op,id_empleado,cedula,nombre,salario,id_nomina) =>{
        setId_empleado('');
        setCedula('');
        setNombre('');
        setSalario('');
        setId_nomina('');
        setOperacion(op);
        if (op===1) {
            setTitle('Registrar Empleado');
        }
        else if(op=== 2){
            setTitle('Editar Empleado');
            setId_empleado(id_empleado);
            setCedula(cedula);
            setNombre(nombre);
            setSalario(salario);
            setId_nomina(id_nomina);

        }
        window.setTimeout(function(){
            document.getElementById('cedula').focus();
        },500);
    }

    const validar = () => {
        var parametros;
        var metodo;
        if (cedula.trim() ==='') {
            Show_alerta('Escribe la cedula del empleado','warning');
        } 
        else if(nombre.trim() === ''){
            Show_alerta('Escribe el nombre del empleado','warning');
        }
        else if(salario === ''){
            Show_alerta('Escribe el salario del empleado','warning');
        }
        else if(id_nomina === ''){
            Show_alerta('Escribe el id de la nomina del empleado','warning');
        }
        else {
            if(operacion === 1){
                parametros= {cedula:cedula.trim(),nombre:nombre.trim(),salario:salario,id_nomina:id_nomina}
                metodo='POST';
            }
            else{
                parametros= {id_empleado:id_empleado,cedula:cedula.trim(),nombre:nombre.trim(),salario:salario,id_nomina:id_nomina}
                metodo='PUT';
            }
            enviarSolicitud(metodo,parametros);
        }
    }
    const enviarSolicitud = async(metodo,parametros) => {
        await axios({method:metodo, url: url, data:parametros}).then(function(respuesta){
            var tipo = respuesta.data[0];
            var msj = respuesta.data[1];
            Show_alerta(msj,tipo);
            if (tipo === 'success'){
                document.getElementById('btnCerrar').click();
                getEmpleados();
            }   
        })
        .catch(function(error){
            Show_alerta('Error en la solicitud', 'error');
            console.log(error);
        });
    }

    const deleteEmpleado= (id_empleado,nombre) =>{
        const MySwal = withReactContent(Swal);
        MySwal.fire({
            title:'Seguro de eliminar al empleado '+nombre+' ?',    
            icon: 'question',text:'No se podra dar marcha atras',
            showCancelButton:true,confirmButtonText:'Si, eliminar',cancelButtonText:'Cancelar'

        }).then((result) =>{
            if(result.isConfirmed){
                setId_empleado(id_empleado);
                enviarSolicitud('DELETE',{id_empleado:id_empleado});
            }
            else{
                Show_alerta('El empleado NO fue eliminado','info');
            }
        });
    }


  return (
    <div className='App'>
        <div className='container-fluid'>
            <div className='row mt-3'>
                <div className='col-md-4 offset-md-4'>
                    <div className='d-grid mx-auto'>
                        <button onClick={()=> openModal(1)} className='btn btn-dark' data-bs-toggle='modal' data-bs-target='#modalempleados'>
                            <i className='fa-solid fa-circle-plus'></i> Aadir Empleado
                        </button>
                    </div>
                </div>
            </div>
            <div className='row mt-3'>
                <div className='col-12 col-lg-8 offset-0 offset-lg-2'>
                    <div className='table-responsive'>
                        <table className='table table-bordered'>
                            <thead>
                                <tr><th>#</th><th>Cedula</th><th>Nombre</th><th>Salario Mensual</th><th>Id Nomina</th></tr>
                            </thead>
                            <tbody className='table-group-divider'>
                                {empleados.map( (empleado)=>(
                                    <tr key={empleados.id_empleado}>
                                        <td>{empleado.id_empleado}</td>
                                        <td>{empleado.cedula}</td>
                                        <td>{empleado.nombre}</td>
                                        <td>${new Intl.NumberFormat('es-mx').format(empleado.salario)}</td>
                                        <td>{empleado.id_nomina}</td>
                                        <td>
                                            <button onClick={() => openModal(2,empleado.id_empleado,empleado.cedula,empleado.nombre,empleado.salario,empleado.id_nomina)} 
                                                    className='btn btn-warning' data-bs-toggle='modal' data-bs-target='#modalempleados'>
                                                <i className='fa-solid fa-edit'></i>
                                            </button>
                                            &nbsp;
                                            <button onClick={()=>deleteEmpleado(empleado.id_empleado,empleado.nombre)} className='btn btn-danger'>
                                                <i className='fa-solid fa-trash'></i>
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody> 
                            
                           
                        </table>
                    </div>
                </div>
            </div>

        </div>
        
        <div id='modalempleados' className='modal fade' aria-hidden='true'>
            <div className='modal-dialog'>
                <div className='modal-content'>
                    <div className='modal-header'>
                        <label className='h5'>{title}</label>
                        <button type='button' className='btn-close' data-bs-dismiss='modal' aria-label='close'></button>
                    </div>
                    <div className='modal-body'>
                        <input type='hidden' id='id'></input>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-credit-card'></i></span>
                            <input type='text' id='cedula' className='form-control' placeholder='Cedula' value={cedula}
                            onChange={(e)=> setCedula(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-person'></i></span>
                            <input type='text' id='nombre' className='form-control' placeholder='Nombre' value={nombre}
                            onChange={(e)=> setNombre(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-dollar-sign'></i></span>
                            <input type='text' id='salario' className='form-control' placeholder='Salario' value={salario}
                            onChange={(e)=> setSalario(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-list-ol'></i></span>
                            <input type='text' id='id_nomina' className='form-control' placeholder='ID Nomina' value={id_nomina}
                            onChange={(e)=> setId_nomina(e.target.value)}></input>
                        </div>
                        <div className='d-grid col-6 mx-auto'>
                            <button onClick={() => validar()}  className='btn btn-success'>
                                <i className='fa-solid fa-floppy-disk'></i>Guardar
                            </button>
                        </div>

                    </div>
                    <div className='modal-footer'>
                        <button type='button' id='btnCerrar' className='btn btn-secondary' data-bs-dismiss='modal'>Cerrar</button>
                    </div>
                </div>
            </div>

        </div>
    </div>
  )
}

export default ShowEmpleado
