import React, {useEffect, useState} from 'react';
import axios from 'axios';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { Show_alerta } from '../functions';

const ShowTransaccion = () => {
    const url='http://api.test/transaccionesController.php';
    const [transacciones,setTransacciones]= useState([]);
    const [id_transaccion,setId_transaccion]= useState('');
    const [id_empleado,setId_empleado]= useState('');
    const [id_deducciones,setId_deducciones]= useState('');
    const [id_ingreso,setId_ingreso]= useState('');
    const [tipo_transaccion,setTipo_transaccion]= useState('');
    const [fecha,setFecha]= useState('');
    const [monto,setMonto]= useState('');
    const [estado,setEstado]= useState('');
    const [operacion,setOperacion]= useState(1);
    const [title,setTitle]= useState('');

    useEffect( ()=>{
        getTrasaccion();
    },[]);

    const getTrasaccion = async () => {
        const respuesta = await axios.get(url);
        setTransacciones(respuesta.data);
    }
   


    const openModal_4 = (op,id_transaccion,id_empleado,id_deducciones,id_ingreso,tipo_transaccion,fecha,monto,estado) =>{
        setId_transaccion('');
        setId_empleado('');
        setId_deducciones('');
        setId_ingreso('');
        setTipo_transaccion('');
        setFecha('');
        setMonto('');
        setEstado('');
        setOperacion(op);
        if (op===1) {
            setTitle('Registrar Transaccion');
        }
        else if(op=== 2){
            setTitle('Editar Empleado');
            setId_transaccion(id_transaccion);
        setId_empleado(id_empleado);
        setId_deducciones(id_deducciones);
        setId_ingreso(id_ingreso);
        setTipo_transaccion(tipo_transaccion);
        setFecha(fecha);
        setMonto(monto);
        setEstado(estado);


        }
        window.setTimeout(function(){
            document.getElementById('id_empleado').focus();
        },500);
    }

    const validar = () => {
        var parametros;
        var metodo;
        if(id_empleado === ''){
            Show_alerta('Escribe el ID del empleado','warning');
        }
        else if(id_deducciones === ''){
            Show_alerta('Escribe el ID de la deduccioon','warning');
        }
        else if(id_ingreso === ''){
            Show_alerta('Escribe el ID del ingreso','warning');
        }
        else if(tipo_transaccion.trim() === ''){
            Show_alerta('Escribe el tipo de transaccion','warning');
        }
        else if(fecha === ''){
            Show_alerta('Escribe la fecha de la transaccion','warning');
        }
        else if(monto === ''){
            Show_alerta('Escribe el monto de la transaccion','warning');
        }
        else if(estado.trim() === ''){
            Show_alerta('Escribe el estado de la transaccion','warning');
        }
        else {
            if(operacion === 1){
                parametros= {id_empleado:id_empleado,id_deducciones:id_deducciones,id_ingreso:id_ingreso,tipo_transaccion:tipo_transaccion.trim(),fecha:fecha,monto:monto,estado:estado.trim()}
                metodo='POST';
            }
            else{
                parametros= {id_transaccion:id_transaccion,id_empleado:id_empleado,id_deducciones:id_deducciones,id_ingreso:id_ingreso,tipo_transaccion:tipo_transaccion.trim(),fecha:fecha,monto:monto,estado:estado.trim()}
                metodo='PUT';
            }
            enviarSolicitud(metodo,parametros);
        }
    }
    const enviarSolicitud = async(metodo,parametros) => {
        await axios({method:metodo, url: url, data:parametros}).then(function(respuesta){
            var tipo = respuesta.data[0];
            var msj = respuesta.data[1];
            Show_alerta(msj,tipo);
            if (tipo === 'success'){
                document.getElementById('btnCerrar').click();
                getTrasaccion();
            }   
        })
        .catch(function(error){
            Show_alerta('Error en la solicitud', 'error');
            console.log(error);
        });
    }

    const deletetransacciones= (id_transaccion,id_empleado) =>{
        const MySwal = withReactContent(Swal);
        MySwal.fire({
            title:'Seguro de eliminar la transaccion '+id_transaccion+' ?',    
            icon: 'question',text:'No se podra dar marcha atras',
            showCancelButton:true,confirmButtonText:'Si, eliminar',cancelButtonText:'Cancelar'

        }).then((result) =>{
            if(result.isConfirmed){
                setId_empleado(id_transaccion);
                enviarSolicitud('DELETE',{id_transaccion:id_transaccion});
            }
            else{
                Show_alerta('La Transaccion NO fue eliminado','info');
            }
        });
    }


  return (
    <div className='App'>
        <div className='container-fluid'>
            <div className='row mt-3'>
                <div className='col-md-4 offset-md-4'>
                
          <br/>
                    <div className='d-grid mx-auto'>
                  
                        <button onClick={()=> openModal_4(1)} className='btn btn-dark' data-bs-toggle='modal' data-bs-target='#modalempleados4'>
                            <i className='fa-solid fa-circle-plus'></i> Aadir Transaccion
                        </button>
                        <br/>
                        <a href="http://api.test/index.php">
                        <button  className='btn btn-dark' >
                            <i className='fa-solid fa-circle-plus'></i> Consulta
                        </button>
                        </a>
                    </div>
                </div>
            </div>
            <div className='row mt-3'>
                <div className='col-12 col-lg-8 offset-0 offset-lg-2'>
                    <div className='table-responsive'>
                        <table className='table table-bordered'>
                            <thead>
                                <tr><th>#</th><th>ID Empleado</th><th>ID Deduccion</th><th>ID Ingreso</th><th>Tipos de transaccion</th><th>Fecha</th><th>Monto</th><th>Estado</th></tr>
                            </thead>
                            <tbody className='table-group-divider'>
                                {transacciones.map( (transaccion)=>(
                                    <tr key={transaccion.id_transaccion}>
                                        <td>{transaccion.id_transaccion}</td>
                                        <td>{transaccion.id_empleado}</td>
                                        <td>{transaccion.id_deducciones}</td>
                                        <td>{transaccion.id_ingreso}</td>
                                        <td>{transaccion.tipo_transaccion}</td>
                                        <td>{transaccion.fecha}</td>
                                        <td>${new Intl.NumberFormat('es-mx').format(transaccion.monto)}</td>
                                        <td>{transaccion.estado}</td>
                                        <td>
                                            <button onClick={() => openModal_4(2,transaccion.id_transaccion,transaccion.id_empleado,transaccion.id_deducciones,transaccion.id_ingreso,transaccion.tipo_transaccion,transaccion.fecha,transaccion.monto,transaccion.estado)} 
                                                    className='btn btn-warning' data-bs-toggle='modal' data-bs-target='#modalempleados4'>
                                                <i className='fa-solid fa-edit'></i>
                                            </button>
                                            &nbsp;
                                            <button onClick={()=>deletetransacciones(transaccion.id_transaccion,transaccion.id_empleado)} className='btn btn-danger'>
                                                <i className='fa-solid fa-trash'></i>
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody> 
                            
                           
                        </table>
                    </div>
                </div>
            </div>

        </div>
        
        <div id='modalempleados4' className='modal fade' aria-hidden='true'>
            <div className='modal-dialog'>
                <div className='modal-content'>
                    <div className='modal-header'>
                        <label className='h5'>{title}</label>
                        <button type='button' className='btn-close' data-bs-dismiss='modal' aria-label='close'></button>
                    </div>
                    <div className='modal-body'>
                        <input type='hidden' id='id'></input>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-person'></i></span>
                            <input type='text' id='id_empleado' className='form-control' placeholder='Id Eempleado' value={id_empleado}
                            onChange={(e)=> setId_empleado(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-clipboard'></i></span>
                            <input type='text' id='id_deducciones' className='form-control' placeholder='Id Deducciones' value={id_deducciones}
                            onChange={(e)=> setId_deducciones(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-dollar-sign'></i></span>
                            <input type='text' id='id_ingreso' className='form-control' placeholder='Id Ingreso' value={id_ingreso}
                            onChange={(e)=> setId_ingreso(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-list-ol'></i></span>
                            <input type='text' id='tipo_transaccion' className='form-control' placeholder='Tipo de Transaccion' value={tipo_transaccion}
                            onChange={(e)=> setTipo_transaccion(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-calendar-day'></i></span>
                            <input type='date' id='fecha' className='form-control' placeholder='Fecha' value={fecha}
                            onChange={(e)=> setFecha(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-dollar-sign'></i></span>
                            <input type='text' id='monto' className='form-control' placeholder='Monto' value={monto}
                            onChange={(e)=> setMonto(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-battery'></i></span>
                            <input type='text' id='estado' className='form-control' placeholder='Estado' value={estado}
                            onChange={(e)=> setEstado(e.target.value)}></input>
                        </div>
                        <div className='d-grid col-6 mx-auto'>
                            <button onClick={() => validar()}  className='btn btn-success'>
                                <i className='fa-solid fa-floppy-disk'></i>Guardar
                            </button>
                        </div>

                    </div>
                    <div className='modal-footer'>
                        <button type='button' id='btnCerrar' className='btn btn-secondary' data-bs-dismiss='modal'>Cerrar</button>
                    </div>
                </div>
            </div>

        </div>
    </div>
  )
}

export default ShowTransaccion

