import React, {useEffect, useState} from 'react';
import axios from 'axios';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { Show_alerta } from '../functions';

const ShowAsiento = () => {
    const url='http://api.test/AsientosContables.php';
    const [asientos_contables,setAsientos_contables]= useState([]);
    const [id_asiento,setId_asiento]= useState('');
    const [descripcion,setDescripcion]= useState('');
    const [id_empleado,setId_empleado]= useState('');
    const [cuenta,setCuenta]= useState('');
    const [tipo_movimiento,setTipo_movimiento]= useState('');
    const [fecha,setFecha]= useState('');
    const [monto,setMonto]= useState('');
    const [estado,setEstado]= useState('');
    const [operacion,setOperacion]= useState(1);
    const [title,setTitle]= useState('');

    useEffect( ()=>{
        getAsientos();
    },[]);

    const getAsientos = async () => {
        const respuesta = await axios.get(url);
        setAsientos_contables(respuesta.data);
    }

    const openModal_5 = (op,id_asiento,descripcion,id_empleado,cuenta,tipo_movimiento,fecha,monto,estado) =>{
        setId_asiento('');
        setDescripcion('');
        setId_empleado('');
        setCuenta('');
        setTipo_movimiento('');
        setFecha('');
        setMonto('');
        setEstado('');
        setOperacion(op);
        if (op===1) {
            setTitle('Registrar Transaccion');
        }
        else if(op=== 2){
            setTitle('Editar Empleado');
            setId_asiento(id_asiento);
            setDescripcion(descripcion);
            setId_empleado(id_empleado);
            setCuenta(cuenta);
            setTipo_movimiento(tipo_movimiento);
            setFecha(fecha);
            setMonto(monto);
            setEstado(estado);


        }
        window.setTimeout(function(){
            document.getElementById('descripcion').focus();
        },500);
    }

    const validar = () => {
        var parametros;
        var metodo;
        if(descripcion === ''){
            Show_alerta('Escribe la descripcion del asiento','warning');
        }
        else if(id_empleado === ''){
            Show_alerta('Escribe el ID del empleado','warning');
        }
        else if(cuenta === ''){
            Show_alerta('Escribe la cuenta del asiento','warning');
        }
        else if(tipo_movimiento.trim() === ''){
            Show_alerta('Escribe el tipo de movimiento','warning');
        }
        else if(fecha === ''){
            Show_alerta('Selecciona la fecha','warning');
        }
        else if(monto === ''){
            Show_alerta('Escribe el imonto','warning');
        }
        else if(estado.trim() === ''){
            Show_alerta('Escribe el estado','warning');
        }
        else {
            if(operacion === 1){
                parametros= {descripcion:descripcion.trim(),id_empleado:id_empleado,cuenta:cuenta,tipo_movimiento:tipo_movimiento.trim(),fecha:fecha,monto:monto,estado:estado.trim()}
                metodo='POST';
            }
            else{
                parametros= {id_asiento:id_asiento,descripcion:descripcion.trim(),id_empleado:id_empleado,cuenta:cuenta,tipo_movimiento:tipo_movimiento.trim(),fecha:fecha,monto:monto,estado:estado.trim()}
                metodo='PUT';
            }
            enviarSolicitud(metodo,parametros);
        }
    }
    const enviarSolicitud = async(metodo,parametros) => {
        await axios({method:metodo, url: url, data:parametros}).then(function(respuesta){
            var tipo = respuesta.data[0];
            var msj = respuesta.data[1];
            Show_alerta(msj,tipo);
            if (tipo === 'success'){
                document.getElementById('btnCerrar').click();
                getAsientos();
            }   
        })
        .catch(function(error){
            Show_alerta('Error en la solicitud', 'error');
            console.log(error);
        });
    }

    const deleteAsiento= (id_asiento,id_empleado) =>{
        const MySwal = withReactContent(Swal);
        MySwal.fire({
            title:'Seguro de eliminar el asiento '+id_asiento+' ?',    
            icon: 'question',text:'No se podra dar marcha atras',
            showCancelButton:true,confirmButtonText:'Si, eliminar',cancelButtonText:'Cancelar'

        }).then((result) =>{
            if(result.isConfirmed){
                setId_empleado(id_asiento);
                enviarSolicitud('DELETE',{id_asiento:id_asiento});
            }
            else{
                Show_alerta('El asiento NO fue eliminado','info');
            }
        });
    }


  return (
    <div className='App'>
        <div className='container-fluid'>
            <div className='row mt-3'>
                <div className='col-md-4 offset-md-4'>
                    <div className='d-grid mx-auto'>
                        <button onClick={()=> openModal_5(1)} className='btn btn-dark' data-bs-toggle='modal' data-bs-target='#modalempleados5'>
                            <i className='fa-solid fa-circle-plus'></i> Aadir Asiento
                        </button>
                    </div>
                </div>
            </div>
            <div className='row mt-3'>
                <div className='col-12 col-lg-8 offset-0 offset-lg-2'>
                    <div className='table-responsive'>
                        <table className='table table-bordered'>
                            <thead>
                                <tr><th>#</th><th>Descripcion</th><th>ID Empleado</th><th>Cuenta</th><th>Tipo de Movimiento</th><th>Fecha</th><th>Monto</th><th>Estado</th></tr>
                            </thead>
                            <tbody className='table-group-divider'>
                                {asientos_contables.map( (asiento,i)=>(
                                    <tr key={asiento.id_asiento}>
                                        <td>{asiento.id_asiento}</td>
                                        <td>{asiento.descripcion}</td>
                                        <td>{asiento.id_empleado}</td>
                                        <td>{asiento.cuenta}</td>
                                        <td>{asiento.tipo_movimiento}</td>
                                        <td>{asiento.fecha}</td>
                                        <td>${new Intl.NumberFormat('es-mx').format(asiento.monto)}</td>
                                        <td>{asiento.estado}</td>
                                        <td>
                                            <button onClick={() => openModal_5(2,asiento.id_asiento,asiento.descripcion,asiento.id_empleado,asiento.cuentta,asiento.tipo_movimiento,asiento.fecha,asiento.monto,asiento.estado)} 
                                                    className='btn btn-warning' data-bs-toggle='modal' data-bs-target='#modalempleados5'>
                                                <i className='fa-solid fa-edit'></i>
                                            </button>
                                            &nbsp;
                                            <button onClick={()=>deleteAsiento(asiento.id_asiento,asiento.id_empleado)} className='btn btn-danger'>
                                                <i className='fa-solid fa-trash'></i>
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody> 
                            
                           
                        </table>
                    </div>
                </div>
            </div>

        </div>
        
        <div id='modalempleados5' className='modal fade' aria-hidden='true'>
            <div className='modal-dialog'>
                <div className='modal-content'>
                    <div className='modal-header'>
                        <label className='h5'>{title}</label>
                        <button type='button' className='btn-close' data-bs-dismiss='modal' aria-label='close'></button>
                    </div>
                    <div className='modal-body'>
                        <input type='hidden' id='id'></input>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-file'></i></span>
                            <input type='text' id='descripcion' className='form-control' placeholder='Descripcion' value={descripcion}
                            onChange={(e)=> setDescripcion(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-credit-card'></i></span>
                            <input type='text' id='id_empleado' className='form-control' placeholder='Id del Empleado' value={id_empleado}
                            onChange={(e)=> setId_empleado(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-bucket'></i></span>
                            <input type='text' id='cuenta' className='form-control' placeholder='Cuenta' value={cuenta}
                            onChange={(e)=> setCuenta(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-list-ol'></i></span>
                            <select id='tipo_movimiento' className='form-control' value={tipo_movimiento}
                            onChange={(e)=> setEstado(e.target.value)}>
                                <option hidden selected>Tipo de Movimiento</option>

                                <option>CR</option>

                                <option selected>DB</option>
                            </select>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-calendar-day'></i></span>
                            <input type='date' id='fecha' className='form-control' placeholder='Fecha' value={fecha}
                            onChange={(e)=> setFecha(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-dollar-sign'></i></span>
                            <input type='text' id='monto' className='form-control' placeholder='Monto' value={monto}
                            onChange={(e)=> setMonto(e.target.value)}></input>
                        </div>
                        <div className='input-group mb-3'>
                            <span className='input-group-text'><i className='fa-solid fa-list-ol'></i></span>
                            <input type='text' id='estado' className='form-control' placeholder='Estado' value={estado}
                            onChange={(e)=> setEstado(e.target.value)}></input>
                        </div>
                        <div className='d-grid col-6 mx-auto'>
                            <button onClick={() => validar()}  className='btn btn-success'>
                                <i className='fa-solid fa-floppy-disk'></i>Guardar
                            </button>
                        </div>

                    </div>
                    <div className='modal-footer'>
                        <button type='button' id='btnCerrar' className='btn btn-secondary' data-bs-dismiss='modal'>Cerrar</button>
                    </div>
                </div>
            </div>

        </div>
    </div>
  )
}

export default ShowAsiento

